
import { defineComponent, computed } from 'vue'
import { ElMessageBox, ElMessage } from 'element-plus'
import axios from 'axios'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'Waiting',
  setup() {
    const participants = ['', '', '', '', '', '']
    const store = useStore()
    const router = useRouter()
    const meetingId = computed(() => store.state.meeting.id)

    const deleteMeeting = function () {
      axios.delete(`/meeting/${meetingId.value}`, 
        { headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }}
      ).then(res => {
        console.log(res)
        router.push({ name: 'Home'})
      }).catch(err => {
        console.log(err.response)
      })
    }

    const clickDeleteMeeting = function () {
      ElMessageBox.confirm(
        ' ?   .',
        ' ',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: '.',
          })
          deleteMeeting()
          store.dispatch('deleteMeeting')
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '.',
          })
        })
    }

    const clickLeaveMeeting = function () {
      ElMessageBox.confirm(
        ' ?',
        ' ',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(() => {
          store.dispatch('deleteMeeting')
          router.push({ name: 'Home'})
          ElMessage({
            type: 'success',
            message: '.',
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '.',
          })
        })
    }
    return { participants, clickDeleteMeeting, clickLeaveMeeting }
  }
})
